.content img,
.content iframe {
  width: auto;
  height: auto;
  max-width: 100%;
  margin-bottom: 2rem; }

.tooltip {
  position: relative;
  cursor: pointer; }
  .tooltip:after {
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s ease-out 0.15s;
    font-size: 14px;
    background: black;
    border-radius: 2px;
    color: #fff;
    content: attr(aria-label);
    padding: 0.2em 0.4em;
    position: absolute;
    white-space: nowrap;
    z-index: 10; }
  .tooltip:before {
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top-color: black;
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s ease-out 0.15s;
    content: "";
    position: absolute;
    z-index: 10; }
  .tooltip:hover:before, .tooltip:hover:after, .tooltip:focus:before, .tooltip:focus:after {
    opacity: 1;
    pointer-events: none; }

.tooltip-n:after {
  bottom: 100%;
  left: 50%;
  margin-bottom: 6px;
  transform: translate(-50%, 4px);
  transform-origin: top; }

.tooltip-n:before {
  bottom: 100%;
